import React, { Component } from 'react';
import { connect } from 'react-redux';
import { submitPostData, showPopUP } from '../../../../../../../../../../actions/themeActions/themeActions';
import QRCode from "react-qr-code";


const validateEmail = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

class SubmitButton extends Component {
    state = { isOpen: false, step: 1, wall_id: null, name: null, email: null, upload_image: [], receiveMail: false, tandc: true, nameError: false, emailError: false }
    onShowCloseSubmitPicPopUp = event => {
        const { wall } = this.props;
        this.props.showPopUP({ type: 'submit-pic', card: {}, personalization: wall?.Personalization, ThemeRule: wall.ThemeRule, UserRule: wall.UserRule, wall: wall })
    }
    updateImageWithData = (data) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length < 5) { upload_image.push(data); this.setState({ upload_image }) }
    }

    onUpdateImageAddCaption = (index) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0 && upload_image[index]) {
            upload_image[index].caption = event.target.value;
            upload_image[index].review = "";
            upload_image[index].rating = 0;
            this.setState({ upload_image })
        }
    }
    onUpdateImageAddReview = (index) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0 && upload_image[index]) {
            upload_image[index].review = event.target.value;
            upload_image[index].caption = "";
            this.setState({ upload_image })
        }
    }

    onUpdateImageAddRatings = (rating, index) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0 && upload_image[index]) {
            upload_image[index].caption = "";
            upload_image[index].rating = rating;
            this.setState({ upload_image })
        }
    }
    onUpdateStep = (step) => event => { this.setState({ step }) }
    onTextName = event => {
        this.setState({ name: event.target.value, nameError: false }, () => {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
        })
    }
    onTextEmail = event => {
        this.setState({ email: event.target.value, emailError: false }, () => {
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || this.state.email.includes("mailinator")) this.setState({ emailError: true })
        })
    }
    onSubmitData = event => {
        const { name, email, upload_image, tandc, receiveMail } = this.state;
        const { wallId } = this.props;
        if (tandc && (name && name.length > 0) && (email && email.length > 0 && validateEmail(email) && !email.includes("mailinator")) && (upload_image && upload_image.length > 0) && tandc) {
            submitPostData({ wall_id: wallId, name, email, upload_image, receive_mail: receiveMail }).then((response) => {
                this.setState({ step: 3 })
            }).catch((error) => {  });
        }
        else {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) this.setState({ emailError: true })
        }
    }
    onUpdatereceiveMail = event => { const { receiveMail } = this.state; this.setState({ receiveMail: !receiveMail }) }
    //onUpdatereceiveMail = event =>this.setState({ receiveMail: !this.state.receiveMail })
    onUpdatetandc = event => { const { tandc } = this.state; this.setState({ tandc: !tandc }) }
    removeProductImage = (removeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0) {
            upload_image.splice(removeIndex, 1);
        }
        this.setState({ upload_image })
    }

    render() {

        const { wall , onsite_status} = this.props;
        const onsite = wall.UgcSettings;
        const onsiteButton = onsite_status ? { backgroundColor: onsite.onsite_btn_color, borderColor: onsite.onsite_btn_color, color: onsite.onsite_btn_txt_color } : '';
        const fontCss = { fontSize: wall.UgcSettings.btn_fontSize + 'px', fontFamily: wall.UgcSettings.onsite_css_font_btn }

        return <>
            {onsite_status !== 0 ?
                <div id="tb_post_submit_container_button" className={wall?.Personalization?.commerceTheme != `50` ? `tb_post_submit_container` : `ts_gst__post_submit_container_button`}>
                    {wall?.Personalization?.commerceTheme != `50` ?
                        <div className="btn-scan">
                            <div style={onsiteButton} className="tb_post_submit_btn" onClick={this.onShowCloseSubmitPicPopUp}>
                                {wall.UgcSettings.onsite_qr_code_status == 1 ? 
                                    <div className='qr-image'>
                                        <QRCode level="L" size={60} value={wall.UgcSettings.onsite_popup_url} />
                                    </div> : null
                                 }
                                {onsite.onsite_qr_code_btn_text ? <>
                                    {wall.UgcSettings.onsite_qr_code_status == 1 ? <div className='ts__qr_separator'><div></div></div> : ''}
                                          <div className={`ts_qr-text  tb-cTSfont-${wall.UgcSettings.onsite_font_varient_btn}`} style={fontCss}>
                                             <div className={`ts_qr-text_wrap tb-cTSfont-${wall.UgcSettings.onsite_font_varient_btn}`} style={fontCss}>
                                                {onsite_status ? onsite.onsite_qr_code_btn_text : onsite.onsite_qr_code_status ? onsite.onsite_qr_code_btn_text : onsite_status ? onsite.onsite_btn_txt : 'Submit a Post'}
                                             </div>
                                         </div> 
                                  </> : ''
                                }
                            </div>
                        </div> :



                        <div className="ts_gst_btn-scan" style={onsiteButton}>
                            <div className="ts_gst_post_submit_btn" onClick={this.onShowCloseSubmitPicPopUp}>
                                <div className='ts_gst_qr-image'>
                                    {wall.UgcSettings.onsite_qr_code_status == `1` ? <QRCode level="L" size={60} value={wall.UgcSettings.onsite_popup_url} /> : ''}
                                    <div className={`ts_gst_qr-text  tb-cTSfont-${wall.UgcSettings.font_varient}`} style={fontCss}>
                                        <div className={`ts_gst_qr-text_wrap ${wall.UgcSettings.onsite_qr_code_status == `1` ? "wt_qrcode" : "wo_qrcode"}`} >
                                            {onsite.onsite_qr_code_status && onsite_status ? onsite.onsite_qr_code_btn_text : onsite.onsite_qr_code_status ? onsite.onsite_qr_code_btn_text : onsite_status ? onsite.onsite_btn_txt : 'Submit a Post'}
                                        </div>
                                    </div>
                                </div>
                                <div className='ts_gst_qr-image_mobile'> Add Your Photo </div>
                            </div>
                        </div>}

                </div> : ''}

        </>
    }
}


const mapDispatchToProps = dispatch => { 
    return { showPopUP: (data) => dispatch(showPopUP(data)), } 
}; 

const mapStateToProps = state => { 
    return { 
        wallId: state.appData.wallID, 
        onsite_status: state.appData.onsite_status, 
    } 
}

export default connect(mapStateToProps, mapDispatchToProps)(SubmitButton);